import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import products from '../products';
import ProductModal from './ProductModal';
import { motion } from 'framer-motion';
import { useInView } from 'react-intersection-observer'; //      

const Product = ({ className }) => {
  const [showModal, setShowModal] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [visibleIndex, setVisibleIndex] = useState(-1); //     (-1   )

  const openModal = (product) => {
    setSelectedProduct(product);
    setShowModal(true);
  };

  const closeModal = () => {
    setSelectedProduct(null);
    setShowModal(false);
  };

  //  useInView    
  const { ref, inView } = useInView({
    triggerOnce: true, //    
    threshold: 0.5 //  : 0.5 ,  50%   
  });

  //        
  useEffect(() => {
    if (inView) {
      const interval = setInterval(() => {
        setVisibleIndex((prevIndex) => {
          const newIndex = prevIndex + 1;
          return newIndex < products.length ? newIndex : prevIndex;
        });
      }, 500); //     ( 500  = 0.5 )

      return () => clearInterval(interval); //     
    }
  }, [inView]);

  return (
    <div className={className} ref={ref}>
      <Swiper
      spaceBetween={0}
     
      breakpoints={{
        640: {
          slidesPerView: 1,
        },
        768: {
          slidesPerView: 2,
        },
        1024: {
          slidesPerView: 3,
        },
        1280: {
          slidesPerView: 4,
        },
        1536: {
          slidesPerView: 5,
        },
      }}
      
    >
        {products.map((product, index) => (
          <SwiperSlide key={product.id}>
            {index <= visibleIndex && ( //     <=  visibleIndex
              <motion.div
                className="product-slide group p-4 hover:shadow-md  relative flex items-center justify-center flex-col"
                initial={{ opacity: 0, y: 50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.3 }} //     0.3 
              >
                <div className='relative'>
                  <img src={product.image} alt={product.title} className="h-[440px] w-full object-contain mb-2" />
                  <div className='flex flex-col w-full absolute bottom-10 -right-5 items-end'>
                    {product.sale && <p className="text-sm bg-red-500 px-5 rounded-full mb-2   text-white">!</p>}
                    <p className="text-sm bg-primary w-1/2 bg-opacity-60 backdrop-blur-md backdrop-filter  text-white rounded-full p-2 text-center ">:
                      {product.price} </p>

                  </div>
                </div>
                <h3 className="text-lg font-semibold mb-1">{product.title}</h3>

                <button
                  onClick={() => openModal(product)}
                  className="text-sm text-white transition-all rounded-full  bg-opacity-60 backdrop-blur-md backdrop-filter bg-primary border border-white p-5 opacity-0 absolute z-50 group-hover:opacity-100 hover:underline focus:outline-none "
                >
                  
                </button>
              </motion.div>
            )}
          </SwiperSlide>
        ))}
      </Swiper>

      <ProductModal
        product={selectedProduct}
        showModal={showModal}
        closeModal={closeModal}
      />
    </div>
  );
};

export default Product;
